@font-face {
  font-family: "robotoblack";
  src: url("../../assets/fonts/roboto-black-webfont.woff2") format("woff2"), url("../../assets/fonts/roboto-black-webfont.woff") format("woff"), url("../../assets/fonts/roboto-black-webfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "robotobold";
  src: url("../../assets/fonts/roboto-bold-webfont.woff2") format("woff2"), url("../../assets/fonts/roboto-bold-webfont.woff") format("woff"), url("../../assets/fonts/roboto-bold-webfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "robotoregular";
  src: url("../../assets/fonts/roboto-regular-webfont.woff2") format("woff2"), url("../../assets/fonts/roboto-regular-webfont.woff") format("woff"), url("../../assets/fonts/roboto-regular-webfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
* {
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  font-size: 10px;
  font-family: "robotoregular";
}

.askForm_phone, .askForm_name, .askForm_question {
  width: 100%;
  height: 50px;
  box-shadow: inset 1px 1px 12px 0 rgba(0, 0, 0, 0.19);
  background-color: rgba(255, 255, 255, 0.33);
  color: #000;
  font-size: 1.6rem;
  padding: 1.4rem 1rem;
  line-height: 1;
  border: none;
}
@media (min-width: 425px) {
  .askForm_phone, .askForm_name, .askForm_question {
    width: 300px;
  }
}
@media (min-width: 767px) {
  .askForm_phone, .askForm_name, .askForm_question {
    width: 350px;
  }
}

.askForm_question {
  resize: none;
  height: 120px;
  font-family: "robotoregular";
}

.askForm_btn, .offer_btn {
  display: inline-block;
  width: 200px;
  height: 46px;
  box-shadow: 0 0 62px 0 #d3be88;
  background-image: linear-gradient(to bottom, #ffdc83 0%, #fbd26a 25%, #c17024 85%, #e3a548 100%);
  color: #000;
  font-family: "robotoregular";
  font-size: 1.6rem;
  padding: 11.4px 35.5px 15.6px 33.2px;
  cursor: pointer;
  border: none;
  text-align: center;
  user-select: none;
  text-decoration: none;
  border-radius: 5px;
}
.askForm_btn:hover, .offer_btn:hover {
  background-image: none;
  background-color: #fff;
}
.askForm_btn:focus, .offer_btn:focus {
  outline: none;
}

.offer {
  width: 340px;
  height: 476px;
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0.6);
  box-shadow: 0 0 74px 0 rgba(39, 17, 10, 0.16);
}
.offer_title {
  color: #000;
  font-size: 2.8rem;
  font-family: "robotoblack";
  width: 241px;
  margin-top: 4.4rem;
  margin-left: 3.2rem;
}
@media (min-width: 767px) {
  .offer_title {
    font-size: 2.8rem;
  }
}
.offer_btn {
  margin-left: 3.2rem;
  margin-bottom: 4.9rem;
}
.offer_image {
  width: 100%;
  height: 248px;
  display: block;
  object-fit: cover;
  object-position: center;
}
.offer_dot {
  position: absolute;
  top: 30px;
  right: 20.4px;
  background-color: #dda046;
  border-radius: 50%;
  height: 27.6px;
  width: 27.6px;
}
.offer:hover {
  background-color: #dda046;
}
.offer:hover .offer_dot {
  background-color: #fff;
}

.advantage {
  display: flex;
  width: 400px;
}
.advantage_images {
  position: relative;
}
.advantage_images-img {
  position: relative;
  display: block;
  width: 60px;
  height: 60px;
  z-index: 4;
}
.advantage_images-dot {
  position: absolute;
  background-color: #dda046;
  border-radius: 50%;
  height: 27.6px;
  width: 27.6px;
  top: 10px;
  right: 0;
  z-index: 0;
}
.advantage_content {
  margin-left: 2.2rem;
}
.advantage_content-title {
  color: #000;
  font-size: 2rem;
  font-family: "robotoblack";
  width: 300px;
}
@media (min-width: 767px) {
  .advantage_content-title {
    font-size: 2rem;
  }
}
.advantage_content-text {
  font-size: 1.4rem;
  color: #000;
  font-family: "robotoregular";
  width: 300px;
}
@media (min-width: 767px) {
  .advantage_content-text {
    font-size: 1.8rem;
  }
}

.contact {
  position: relative;
}
.contact_icon {
  position: absolute;
  top: -61px;
}
.contact_text {
  font-size: 1.5rem;
  color: #fff;
  font-family: "robotobold";
  margin-left: 10rem;
  width: 210px;
}
@media (min-width: 767px) {
  .contact_text {
    font-size: 1.8rem;
  }
}

.askForm {
  width: 100%;
  height: 570px;
  box-shadow: 0 0 74px 0 rgba(39, 17, 10, 0.16);
  background-color: rgba(255, 255, 255, 0.6);
  padding-left: 4rem;
  padding-top: 6.7rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
@media (min-width: 767px) {
  .askForm {
    width: 428px;
  }
}
.askForm_title {
  color: #000;
  font-size: 3.6rem;
  font-family: "robotoblack";
}
@media (min-width: 767px) {
  .askForm_title {
    font-size: 3.6rem;
  }
}
.askForm_text {
  font-size: 1.4rem;
  color: #000;
  font-family: "robotoregular";
  line-height: 1.5;
  width: 100%;
}
@media (min-width: 767px) {
  .askForm_text {
    font-size: 1.8rem;
  }
}
@media (min-width: 767px) {
  .askForm_text {
    width: 350px;
  }
}
.header {
  height: 220px;
}
@media (min-width: 1024px) {
  .header {
    height: 150px;
  }
}
.header_wrapper {
  width: 100%;
  background-color: #000;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .header_wrapper {
    height: 210px;
    flex-direction: row;
  }
}
.header_logo {
  font-size: 4.8rem;
  color: #fff;
  font-family: "robotobold";
  line-height: normal;
  margin-left: 6.3rem;
  margin-top: 1.8rem;
}
@media (min-width: 767px) {
  .header_logo {
    font-size: 4.8rem;
  }
}
@media (min-width:1440px) {
  .header_logo {
    margin-left: 15.3rem;
  }
}
.header_contacts {
  display: flex;
  flex-direction: column;
  margin-top: 1.5rem;
  margin-left: 0;
  gap: 1rem;
}
@media (min-width: 767px) {
  .header_contacts {
    flex-direction: row;
    gap: 0;
    margin-left: 5rem;
    margin-top: 3.5rem;
  }
}

.slider {
  background-color: #000;
}
.slider_content {
  height: 800px;
  width: 100%;
}
@media (min-width: 767px) {
  .slider_content {
    height: 600px;
  }
}
.slider_content-item {
  width: 100%;
  height: 100%;
  background-color: black;
}
.slider_content-item img {
  display: block;
  object-fit: fill;
  width: 100%;
  height: 100%;
}

.offers_wrapper {
  background-color: #f4f4f4;
}
.offers_title {
  color: #000;
  font-size: 4rem;
  font-family: "robotoregular";
  text-align: center;
  padding-top: 6rem;
}
@media (min-width: 767px) {
  .offers_title {
    font-size: 4.8rem;
  }
}
.offers_content {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 6rem;
  padding-bottom: 6rem;
}

.advantages {
  background-color: #f4f4f4;
}
.advantages_elements {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 767px) {
  .advantages_elements {
    flex-direction: row;
    flex-wrap: wrap;
  }
}
.advantages_title {
  color: #000;
  font-size: 4rem;
  font-family: "robotoregular";
  text-align: center;
  padding-top: 6rem;
  margin-bottom: 6rem;
}
@media (min-width: 767px) {
  .advantages_title {
    font-size: 4.8rem;
  }
}

.examples {
  background-color: #f4f4f4;
  padding-bottom: 6rem;
}
.examples_title {
  color: #000;
  font-size: 4rem;
  font-family: "robotoregular";
  text-align: center;
  padding-top: 6rem;
  margin-bottom: 6rem;
}
@media (min-width: 767px) {
  .examples_title {
    font-size: 4.8rem;
  }
}
.examples_content {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 3px;
}
.examples_content-img {
  width: 453px;
  height: 426px;
}

.contacts_wrapper {
  background: linear-gradient(rgba(255, 255, 255, 0.46), rgba(255, 255, 255, 0.46)), url(../../assets/images/slider_images/d1893b94-5cd9-48ea-9d41-7926c13228b9.jpg) left 50% bottom 100%/cover no-repeat fixed;
  height: 817px;
}
.contacts_title {
  color: #000;
  font-size: 4rem;
  font-family: "robotoregular";
  text-align: center;
  padding-top: 6rem;
  margin-bottom: 6rem;
}
@media (min-width: 767px) {
  .contacts_title {
    font-size: 4.8rem;
  }
}
.contacts_content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 1024px) {
  .contacts_content {
    flex-direction: row;
    justify-content: space-evenly;
  }
}
.contacts_content .contactsSection {
  width: 100%;
  height: 570px;
  box-shadow: 0 0 74px 0 rgba(39, 17, 10, 0.16);
  background-color: rgba(255, 255, 255, 0.6);
  padding-left: 4rem;
  padding-top: 6.7rem;
}
@media (min-width: 1024px) {
  .contacts_content .contactsSection {
    width: 900px;
  }
}
.contacts_content .contactsSection_contacts {
  display: flex;
  flex-wrap: wrap;
}
.contacts_content .contactsSection_contacts .contact > .contact_text {
  font-size: 1.5rem;
  color: #000;
  font-family: "robotobold";
}
@media (min-width: 767px) {
  .contacts_content .contactsSection_contacts .contact > .contact_text {
    font-size: 1.8rem;
  }
}
.contacts_content .contactsSection_contacts .contact {
  margin-left: -3rem;
}
.contacts_content .contactsSection_img {
  height: 400px;
  width: 100%;
  margin-top: 2rem;
  object-fit: cover;
  object-position: center;
}
@media (min-width: 1024px) {
  .contacts_content .contactsSection_img {
    width: 580px;
  }
}
@media (min-width:1440px) {
  .contacts_content .contactsSection_img {
    width: 680px;
  }
}

.footer {
  height: 100px;
}
.footer_text {
  font-size: 1.4rem;
  color: #fff;
  font-family: "robotoregular";
  padding-top: 5rem;
}
@media (min-width: 767px) {
  .footer_text {
    font-size: 1.8rem;
  }
}

html, body {
  background: #fff;
}

main, header, footer {
  width: 100%;
  background-color: #000;
}

.container {
  margin: 0 auto;
  width: 100%;
}
@media (min-width:1440px) {
  .container {
    width: 1366px;
  }
}

h1 {
  font-size: 4.8rem;
  color: #000;
  font-family: "robotobold";
  line-height: normal;
}
@media (min-width: 767px) {
  h1 {
    font-size: 4.8rem;
  }
}

p {
  font-family: "robotoregular";
}